.header {
    background-image: url('../img/header-bg.png');
    height: 840px;
    position: relative;
    &__scroll {
        position: absolute;
        left: 48%;
        bottom: -33px;
        width: 66px;
        height: 66px;
        border: 1px solid #fff;
        border-radius: 50%;
        background-color: #fff;
        background-image: url('../img/scroll-down.png');
        background-repeat: no-repeat;
        background-position: center;
        box-shadow: 0 6px 22px rgba(0, 0, 0, 0.15);
        cursor: pointer; } }
.logo {
    float: left; }
.main-nav {
    float: right;
    &__item {
        display: inline-block; }
    &__link {
        padding: 0 25px;
        color: #fffefe;
        font-family: Roboto;
        font-size: 15px;
        font-weight: 400;
        line-height: 44px; }
    &__log {
        padding: 0 25px;
        border-left: 1px solid #fff;
        color: #fffefe;
        font-family: Roboto;
        font-size: 15px;
        font-weight: 700;
        line-height: 44px;
        text-transform: uppercase; }
    &__sign {
        padding: 17px 25px;
        color: #fffefe;
        font-family: Roboto;
        font-size: 15px;
        font-weight: 700;
        line-height: 44px;
        text-transform: uppercase;
        border-radius: 21px;
        border: 2px solid #fff; } }
.slider {
    &__content {
        margin-top: 229px; }
    &__text {
        color: #e2f0ff;
        font-family: Roboto;
        font-size: 22px;
        font-weight: 400;
        line-height: 44px; } }
h1 {
    color: #fff;
    font-family: Montserrat;
    font-size: 52px;
    font-weight: 900;
    line-height: 64px;
    text-transform: uppercase; }
.button {
    padding: 20px 35px;
    box-shadow: 0 6px 22px rgba(0, 0, 0, 0.35);
    border-radius: 29px;
    background-color: #00be9d;
    color: #fff;
    font-family: Roboto;
    font-size: 16px;
    font-weight: 500;
    line-height: 44px;
    text-transform: uppercase;
    &_gradient {
        padding: 25px;
        background-image: linear-gradient(to right, #00be9d 0%, #0082f9 100%); } }
