html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

* {
  margin: 0;
  padding: 0;
}

body {
  background: #FFF;
  font-size: 50px;
  font-size: calc(16px + (50 - 16) * ((100vw - 600px) / (1000 - 600)));
  line-height: 1.4;
}

@media (max-width: 600px) {
  body {
    font-size: 16px;
  }
}

@media (min-width: 1000px) {
  body {
    font-size: 50px;
  }
}

.container {
  margin: 0 auto;
  width: 1170px;
}

a {
  text-decoration: none;
}

.clearfix {
  clear: both;
}

section {
  padding: 90px 0 68px 0;
}

.header {
  background-image: url("../img/header-bg.png");
  height: 840px;
  position: relative;
}

.header__scroll {
  position: absolute;
  left: 48%;
  bottom: -33px;
  width: 66px;
  height: 66px;
  border: 1px solid #fff;
  border-radius: 50%;
  background-color: #fff;
  background-image: url("../img/scroll-down.png");
  background-repeat: no-repeat;
  background-position: center;
  box-shadow: 0 6px 22px rgba(0, 0, 0, 0.15);
  cursor: pointer;
}

.logo {
  float: left;
}

.main-nav {
  float: right;
}

.main-nav__item {
  display: inline-block;
}

.main-nav__link {
  padding: 0 25px;
  color: #fffefe;
  font-family: Roboto;
  font-size: 15px;
  font-weight: 400;
  line-height: 44px;
}

.main-nav__log {
  padding: 0 25px;
  border-left: 1px solid #fff;
  color: #fffefe;
  font-family: Roboto;
  font-size: 15px;
  font-weight: 700;
  line-height: 44px;
  text-transform: uppercase;
}

.main-nav__sign {
  padding: 17px 25px;
  color: #fffefe;
  font-family: Roboto;
  font-size: 15px;
  font-weight: 700;
  line-height: 44px;
  text-transform: uppercase;
  border-radius: 21px;
  border: 2px solid #fff;
}

.slider__content {
  margin-top: 229px;
}

.slider__text {
  color: #e2f0ff;
  font-family: Roboto;
  font-size: 22px;
  font-weight: 400;
  line-height: 44px;
}

h1 {
  color: #fff;
  font-family: Montserrat;
  font-size: 52px;
  font-weight: 900;
  line-height: 64px;
  text-transform: uppercase;
}

.button {
  padding: 20px 35px;
  box-shadow: 0 6px 22px rgba(0, 0, 0, 0.35);
  border-radius: 29px;
  background-color: #00be9d;
  color: #fff;
  font-family: Roboto;
  font-size: 16px;
  font-weight: 500;
  line-height: 44px;
  text-transform: uppercase;
}

.button_gradient {
  padding: 25px;
  background-image: linear-gradient(to right, #00be9d 0%, #0082f9 100%);
}

.main {
  padding: 113px 0 89px 0;
  border-bottom: 1px solid #f3f3f3;
}

.h2 {
  margin-bottom: 50px;
  text-align: center;
  color: #181818;
  font-family: Montserrat;
  font-size: 32px;
  font-weight: 900;
  line-height: 42px;
  text-transform: uppercase;
}

.list {
  display: flex;
  justify-content: space-around;
  text-align: center;
}

.list__item {
  flex-basis: 250px;
}

.list__h4 {
  font-weight: 400;
  color: #181818;
  font-family: Montserrat;
  font-size: 18px;
  line-height: 44px;
}

.list__text {
  color: #585858;
  font-family: Roboto;
  font-size: 14px;
  line-height: 24px;
}

.about {
  border-bottom: 2px solid rgba(243, 243, 243, 0.85);
}

.description {
  height: 657px;
  position: relative;
  background-image: url("../img/descr-bg.png");
  background-repeat: no-repeat;
  background-position: left bottom;
}

.description__text {
  margin-top: 10%;
  width: 434px;
  float: right;
  color: #585858;
  font-family: Roboto;
  font-size: 16px;
  line-height: 28px;
  font-weight: 500;
}

.offer__text {
  margin-bottom: 45px;
  color: #585858;
  font-family: Roboto;
  font-size: 16px;
  font-weight: 500;
  line-height: 28px;
  text-align: center;
}

.listings {
  height: 300px;
  background-image: url("../img/listings-bg.png");
}

.listings__list {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.listings__h3 {
  color: #fefefe;
  font-family: Montserrat;
  font-size: 26px;
  font-weight: 900;
  line-height: 38px;
  text-transform: uppercase;
}

.listings__text {
  color: #fff;
  font-family: Roboto;
  font-size: 16px;
  font-weight: 500;
  line-height: 28px;
  text-transform: uppercase;
}

.footer {
  padding: 49px 0 25px 0;
}

.footer__container {
  display: flex;
  justify-content: space-between;
}

.footer__about {
  width: 465px;
}

.footer__text {
  color: #737171;
  font-family: Roboto;
  font-size: 14px;
  font-weight: 400;
  line-height: 24px;
}

.footer__link {
  color: #737171;
  font-family: Roboto;
  font-size: 14px;
  font-weight: 400;
}

.network__item {
  display: inline-block;
}

.h4 {
  color: #313131;
  font-family: Montserrat;
  font-size: 15px;
  font-weight: 700;
  line-height: 44px;
  text-transform: uppercase;
}
